import react, {Component} from 'react';
import {Container} from'bloomer'

const logos ={
    backgroundColor: 'white',
    height: '40px',
    padding: '10px'
}

export default class Login extends Component{

    render(){
        return(
            <div style={logos}>
            <Container>
                <p>Username:</p>
                </Container></div>
        )
    }

}


